
.Nav {
    display: flex;
    flex-wrap: wrap;
    position: sticky;
    z-index: 1;
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
    background-color: rgb(66, 66, 67, 98%);
    box-shadow: 0 5px 5px 5px rgba(246, 246, 246, 0.1);

}

.navbar {
    display: flex;
    justify-content: center;
    margin: 0 auto;
    padding: 5px;
    width: 80%;
    height: 50px;
    flex-wrap: nowrap;
    align-content: center;
    align-items: center;


}

.logo {
    display: flex;
    padding: 10px;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: auto;
    cursor: pointer;
    margin: 0 20px;
}

.logo > img {
    max-height: 40px;

}

.user-logo-list {
    display: flex;
    flex-direction: row;
    list-style: none;
    align-items: center;
    gap: 3px;

}

.user-logo {
    max-height: 30px;

}


.logo > h1 {
    color: #d8d8d8;
    font-size: 30px;
    font-weight: 500;
    font-family: 'Roboto', sans-serif;
    transition: all 0.3s ease-in-out;
    margin-left: 10px;

}

.logo > h1:hover {
    color: #ffffff;

}

.nav-items > ul {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: auto;
    height: 100%;
    padding: 0;
    list-style: none;
    margin: 0 20px;
}

.nav-items > ul > li {
    display: flex;
    align-items: center;
    justify-content: center;
    width: auto;
    height: 100%;
    padding: 10px;
    list-style: none;
    margin: 0 20px;

}

.nav-items > ul > li > a {
    text-decoration: none;
    color: #d8d8d8;
    font-size: 17px;
    font-family: 'Roboto', sans-serif;
    transition: all 0.3s ease-in-out;
}

.nav-items > ul > li > a:hover {
    color: #ffffff;
    transition: all 0.3s ease-in-out;
}

.nav-items > ul > li > a:active {
    color: #ffffff;
    transition: all 0.3s ease-in-out;
}

.login_button {
    border: 1px solid #c8d6d6;
    color: #c8d6d6;
    border-radius: 10px;
    align-items: center;
    padding: 5px 10px;
}

.logout_button {
    border: 1px solid #c8d6d6;
    color: #ffffff;
    border-radius: 10px;
    align-items: center;
    padding: 5px 10px;
}

.logout_button:hover {
    border: 1px solid #ffffff;
    color: #ffffff;
    transition: all 0.3s ease-in-out;
}

.login_button:hover {
    border: 1px solid #ffffff;
    color: #ffffff;
    transition: all 0.3s ease-in-out;
}


