/*#################################courses####################################*/

.courses {

    width: 100%;
    margin: 0;
    padding: 50px 0;
    margin-top: 50px;
    background-image: url("./img/pc.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.cards {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap;
    gap: 10px;


}

.card {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 22%;
    height: 300px;
    max-height: 400px;
    background-color: #454546;
    border-radius: 10px;
