}

.imgs {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.img1 {
    background-image: url("./img/pxfuel.jpg");

}

.img2 {
    background-image: url("./img/pxfuel (1).jpg");

}

.img3 {
    background-image: url("./img/bag2.jpg");

}

.img4 {
    background-image: url("./img/aaa.jpg");

}


.card:hover {
    box-shadow: 0px 0px 30px 0px rgb(255, 255, 255);
    background-color: rgb(66, 66, 67, 90%);
    transform: scale(1.08);


}

.card > h2 {
    color: #454546;
    font-size: 1.2rem;
    transition: transform .6s;
    background-color: #d6d6d6;
    font-family: "Arial Black", sans-serif;
