    border-radius: 10px;

}

.card > h2:hover {
    color: #000000;

}

.about {
    width: 100%;
    height: auto;
    padding: 20px;
    background-color: #dadad8;
    margin: 0;
    padding-bottom: 80px;
    box-shadow: -5px 40px 30px 0 #a6a6a6;
    background-image: url("./img/back2.jpg");
    background-size: cover;
    background-repeat: no-repeat;


}

.about-content {
    display: flex;
    width: 100%;
    height: auto;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    align-content: center;
    padding: 5px;
    padding-right: 8rem;
    margin: 10px;

}

.about-text {
    display: flex;
    width: 100%;
    flex-direction: column;
    justify-content: space-between;
}

.about-text > p {
    letter-spacing: 3px;
}

.about-text > h2 {
    letter-spacing: 2px;
    font-size: 2rem;
    opacity: 80%;
    font-family: sans-serif;
    color: #454546;
    margin-bottom: 40px
}

.about-text > button {
    text-decoration: none;
    color: #ffffff;
    background-color: rgb(66, 66, 67, 0.8);
    font-size: 17px;
    transition-duration: 2s;
    border-radius: 10px;
    padding: 5px;
    max-width: 130px;

}

.about-text > button:hover {
    color: white;
    background-color: rgba(106, 106, 107, 0.6);
    transition: all 0.8s ease-in-out;
    box-shadow: rgb(248, 248, 248) 0px 0px 0px 2px;
    border-radius: 25px;
    padding: 5px 25px;
    max-width: 222px;

}

.about-video {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.about-video > .iframe {

    width: 500px;
    height: auto;
    max-height: 400px;
    min-height: 240px;
    border-radius: 15px;
    box-shadow: 0 0 60px 0 rgb(98, 97, 97);
    transition: all 0.8s ease-in-out;
    cursor: pointer;

}

.about-video > .iframe:hover {
    transform: scale(1.05);
    box-shadow: 0 0 30px 0 rgb(187, 187, 187);
}

.popup-iframe {
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 99;
    display: flex;
    flex-direction: column;
    justify-content: center;
    top: 0;
    left: 0;
    transition-duration: 2s;
    background-color: rgb(0, 0, 0);
    box-shadow: black;


}

.popup-back-top {
    z-index: 91;
    position: fixed;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    width: 100%;
    max-height: 200px;
    top: 30px;
    transition-duration: 2s;
    opacity: 80%;
}


.popup-back-flex {
    display: flex;
    justify-content: center;
    width: 100%;
    transition-duration: 5s;
}


.popup-back {
    display: inline-block;
    text-align: center;
    align-content: center;
    font-family: "Arial Black", sans-serif;
    font-size: 20px;
    border-radius: 20px;
    padding: 5px 90px;
    margin-top: 2px;
    cursor: pointer;
    color: white;
    background-color: #3a3939;
    opacity: 90%;
    transition-duration: 2s;


}

.popup-back:hover {
    transform: scale(1.05);
    box-shadow: 0 0 30px 0 rgb(187, 187, 187);
    transition-duration: 2s;
}

.popup-iframe_iframe {
    margin: 10px auto;
    width: 100%;
    height: 95%;
    border: none;
    z-index: 100;

}

.popup-iframe iframe:hover {
    z-index: 90;
    cursor: pointer;
    box-shadow: 0 0 30px 0 rgb(0, 0, 0);
    transition-duration: 2s;
}


