}

.job-card h3 {
    color: #f8f8f8;
    background-color: rgb(66, 66, 67, 80%);
    border-radius: 10px;
    padding: 5px;


}

.job-card a {
    text-decoration: none;
    color: #ffffff;
    background-color: rgb(66, 66, 67, 80%);
    font-size: 17px;
    transition-duration: 3s;
    border-radius: 10px;
    padding: 5px;
}

.job-card a:hover {
    color: white;
    background-color: rgb(66, 66, 67, 60%);
    transition: all 0.4s ease-in-out;
    box-shadow: rgb(248, 248, 248) 0px 0px 0px 2px;
    border-radius: 25px;
    padding: 5px 25px;
}


.job-card:hover {
    transform: scale(1.1);
    border: 1px solid #8a8989;
    box-shadow: 0px 0px 40px 0px rgb(162, 162, 166);
    opacity: 100%;
    transition: all 0.4s ease-in-out;

}

.job-card > h2 {
    color: #d3d5d7;
    font-size: 1.5rem;
    transition: transform 1s;
}

.job-card > h2:hover {
    color: #ffffff;

}

